import React from 'react'
import { Link, StaticQuery, graphql } from 'gatsby'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faTwitterSquare,
  faFacebookSquare,
  faInstagram,
  faLinkedin,
  faSnapchatSquare,
} from '@fortawesome/free-brands-svg-icons'

const HeaderSocialLinks = ({ className }) => (
  <StaticQuery
    query={graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
            socialLinks {
              linked_in
              twitter
              facebook
              snapchat
              instagram
            }
          }
        }
      }
    `}
    render={data => {
      const {
        linked_in,
        twitter,
        facebook,
        snapchat,
        instagram,
      } = data.site.siteMetadata.socialLinks

      const renderIconLink = (path, icon, key) => (
        <li key={key}>
          <Link to={path}>
            <FontAwesomeIcon size="lg" icon={icon} />
          </Link>
        </li>
      )
      return (
        <ul className={`right ${className}`}>
          {[
            linked_in
              ? renderIconLink(linked_in, faLinkedin, 'linked_in')
              : null,
            twitter
              ? renderIconLink(twitter, faTwitterSquare, 'twitter')
              : null,
            facebook
              ? renderIconLink(facebook, faFacebookSquare, 'facebook')
              : null,
            snapchat
              ? renderIconLink(snapchat, faSnapchatSquare, 'snapchat')
              : null,
            instagram
              ? renderIconLink(instagram, faInstagram, 'instagram')
              : null,
          ]}
        </ul>
      )
    }}
  />
)

export default HeaderSocialLinks
