import React from 'react'
import { StaticQuery, graphql, Link } from 'gatsby'

const PrimaryNavLinks = props => (
  <StaticQuery
    query={graphql`
      query {
        allSitePage(filter: { path: { regex: "/global/" } }) {
          edges {
            node {
              path
            }
          }
        }
      }
    `}
    render={data => {
      let { edges } = data.allSitePage
      return (
        <>
          <li key="home">
            <Link to="/">Home</Link>
          </li>
          {edges.map(e => (
            <li key={e.node.path}>
              <Link to={e.node.path}>Link</Link>
            </li>
          ))}
        </>
      )
    }}
  />
)

export default PrimaryNavLinks
